*{
  box-sizing: border-box;
}

.Masthead{
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
  text-align: center;
}

.Masthead img{
  width: 100%;
}


.Navbar{
  width: 100%;
  text-align: center;
}

.Sidebar{
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: green solid 1px;
  text-align: center;
}

.sidebar-article{
  width: 100%;
  border: lawngreen solid 1px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
}

.page{
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 1px red solid
}

.article{
  width: 100%;
  border: 1px pink solid;
  /* display: flex; */
  /* flex-direction: column; */
  /* flex-wrap: wrap; */
  justify-content: space-between;
}

.wrapper{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

